:root {
    --dark-background-color: #343a40;
    --dark-text-color: #f8f9fa;
    --dark-text-color-2: #e0e1dd;
    --dark-h1-color: #d9dee2;
    --dark-blockquote-background-color: #626468;
    --dark-blockquote-text-color: #ced4da;
    --dark-code-background-color: #778da9;
    --dark-code-text-color: #ced4da;
}


body.theme-dark{
    background-color: #343a40;
    background-color: var(--dark-background-color);
    color: #f8f9fa;
    color: var(--dark-text-color);
}

.theme-dark .home-page-title{
    color: #e0e1dd !important;
    color: var(--dark-text-color-2) !important;
}

.theme-dark h1 {
    color: #d9dee2;
    color: var(--dark-h1-color);
}

.theme-dark a {
    color: #e0e1dd;
    color: var(--dark-text-color-2);
}

.theme-dark small{
    color: white !important;
}

.theme-dark #MD blockquote{
    background-color: #626468 !important;
    background-color: var(--dark-blockquote-background-color) !important;
    color: #ced4da !important;
    color: var(--dark-blockquote-text-color) !important;   
}

.theme-dark #MD code {
    background-color: #778da9 !important;
    background-color: var(--dark-code-background-color) !important;
    color: #ced4da;
    color: var(--dark-code-text-color);
}

.theme-dark #MD pre code{
    /* fix color */
    background-color: rgb(45, 45, 45) !important; 
}

.theme-dark input{
    background-color: #343a40;
    background-color: var(--dark-background-color);
}

.theme-dark #Search-Bar button[type=submit]{
    background-color: #f8f9fa;
    background-color: var(--dark-text-color);
}


.theme-dark #MD-Preview{
    overflow: hidden;
    line-height: 20px;
    margin-top: -20px;
    color: #f8f9fa;
    color: var(--dark-text-color);
}

.theme-dark #MD-Preview a{
    color: #f8f9fa;
    color: var(--dark-text-color);
}
html, body, #root {
  margin: 0;
  font-family: "Iansui094-Regular";
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

.render-container{
  margin: 0 auto;
  max-width: 800px;
  padding-bottom: 200px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.text-center {
  text-align: center;
}

/* width */
::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 8px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

a{
  color: #3563bd;
}

a:link{
  text-decoration: none;
}

@media screen and (max-width: 576px) {
  .render-container{
    margin: 0 auto;
    max-width: 800px;
    padding-bottom: 100px;
    padding-left: 3.5%;
    padding-right: 3.5%;
  }
  
  .app-container{
    padding-bottom: 100px;
  }
}
