#App .context {
    max-width: 800px;
    min-width: 600px;
    margin: 0 auto;
    position: relative;
}

#App h1 {
    font-size: 32px;
}

#App .home-page-title {
    color: black;
    text-decoration: none;
}

#App ol {
    display: block;
    list-style-type: decimal;
    margin-top: 1em;
    margin-bottom: 1em;
    margin-left: 0;
    margin-right: 0;
    padding-left: 40px;
}

#App ul {
    display: block;
    list-style-type: disc;
    margin-top: 1em;
    margin-bottom: 1 em;
    margin-left: 0;
    margin-right: 0;
    padding-left: 40px;
}

@media screen and (max-width: 768px) {
    #App .context {
        width: 100%;
        margin: 0 auto;
        position: relative;

    }
}

@media screen and (max-width: 576px) {
    #App .context {
        margin-left: 0px;
        box-sizing: border-box;
        max-width: 576px;
        min-width: 380px;
        position: relative;
    }
}
#Post-Block{
    padding: 15px;
    margin-bottom: 50px;
    border-color: #d8d8d8;
    border-style: solid;
    border-width: 0px 0px 1px 0px;
    border-radius: 5px;
}

#Post-Block hr{
    border: 1px solid #336699;
    margin-bottom: 12px;
}

#Post-Block small{
    color: #444444;
}

#Post-Block .post-date{
    color: rgb(107,114,128);
}

#Post-Block .title-badge{
    width: 100px;
    margin-left: 10px;
    position: relative;
    top: 4px;
}

#Post-Block a.post-title{
    font-size: 18px;
}

@media screen and (max-width: 768px) {
    #Post-Block{
        padding: 12px;
        max-width: 100%;     
    }
}

@media screen and (max-width: 576px) {
    #Post-Block{
        width: 100%;
        padding: 12px;
        box-sizing: border-box;
        margin: 0 auto;
        margin-bottom: 20px;
    }
}
#Search-Bar {
    margin-top: 16px;
    margin-bottom: 32px;
}

#Search-Bar input[type=text] {
    border-color: #b7b7b7;
    border-style: solid;
    border-width: 0px 0px 1px 0px;
    height: 24px;
    padding-left: 5px;
    margin-right: 5px;
}

#Search-Bar input[type=text]:focus{
    outline-width: 0px;
}

#Search-Bar button[type=submit]{
    height: 28px;
    background-color: #e3e3e3;
    border-color: #b7b7b7;
    border-style: solid;
    border-width: 0px 0px 0px 0px;
}
#MD-Preview{
    overflow: hidden;
    line-height: 1.5;
    margin-top: -20px;
    color: rgb(72, 72, 72);
}

#MD-Preview p{
    margin-bottom: 8px;
}

#MD-Preview pre div{
    background: rgb(88, 88, 88) !important;
}
#MD-Preview a{
    color: rgb(59, 59, 59);
}
#MD-Preview document-info,
#MD-Preview .md-preview h1{
    visibility: hidden;
    line-height: 0px;
}

#Loading{
    position: absolute;
    top: 10px;
    left: 10px;
}
.drkmd-toggle-button{
    visibility: hidden !important;
}

#Corner-Menu {
    color: gray;
    font-size: 3px;
    position: fixed;
    bottom: 75px;
    right: 2.5%;
}

#Corner-Menu .link {
    height: 42px;
    width: 42px;
    border-radius: 50%;
    display: block;
    margin-top: 10px;
}

#Corner-Menu .link svg,
#Corner-Menu .link a {
    color: #b7b7b7;
    background-color: rgba(0, 0, 0, 0);
    border: none;
}

#Corner-Menu .link svg:hover,
#Corner-Menu .link a:hover{
    color: gray;
    cursor: pointer;
}

@media screen and (max-width: 768px) {
    #Corner-Menu {
        color: gray;
        font-size: 3px;
        position: fixed;
        bottom: 30px;
        right: 6%;
    }

    #Corner-Menu .link {
        height: 36px;
        width: 36px;
        border-radius: 50%;
        display: block;
        margin-top: 15px;
    }
}
#Footer{
    text-align: center;
    color: gray;
    font-size: 10pt;
    position: fixed;
    bottom: 2.5%;
    right: 2.5%;
}

#Footer a{
    color: #6699FF;
}

#Footer a:link{
    text-decoration: none;
}

#Footer div{
    margin-bottom: 5px;
}


@media screen and (max-width: 768px){
    #Footer{
        color: gray;
        font-size: 10pt;
        position: initial;
    }
}
#MD {
    line-height: 20px;
}

#MD ol {
